import React from "react";
import { NavLink } from "react-router-dom";
import style from './Users.module.css'

const User = ({ user, isFollowingInProgress, follow, unFollow }) => { //Деструктаризация 
  return (

    <div className={style.user}>
      <div className={style.user__col}>
        <NavLink to={'/profile/' + user.id}>
          <div className={style.user__photo}>
            {
              user.photos.small != null
                ? <img src={user.photos.small} alt={user.name}></img>
                : <img src='https://t4.ftcdn.net/jpg/00/64/67/63/360_F_64676383_LdbmhiNM6Ypzb3FM4PPuFP9rHe7ri8Ju.jpg' alt={user.name}></img>
            }
          </div>
        </NavLink>
        <div className={style.user__fullname}>
          {user.name}
        </div>
        <div className={style.user__follow}>

          {user.followed
            ? <button
              disabled={isFollowingInProgress.some(id => id === user.id)}
              onClick={() => {

                unFollow(user.id);
              }
              }>UnFollow</button>
            : <button
              disabled={isFollowingInProgress.some(id => id === user.id)}
              onClick={() => {
                follow(user.id);
              }}>Follow</button>
          }

        </div>
      </div>
      <div className={style.user__col}>
        <div className={style.user__status}>
          {user.status}
        </div>
        <div className={style.location}>
          <div className={style.location__city}>
          </div>
          <div className={style.location__country}>
          </div>
        </div>
      </div>
    </div>

  );
}

export default User;