import React from 'react';
import { connect } from 'react-redux';
import { changePageNumber, follow, requestUsers, setCurrentPage, toggleIsFollowingProgress, unFollow } from '../../redux/users-reducer';
import Preloader from '../Common/Preloader/Preloader';
import Users from './Users';
import { compose } from 'redux';
import {getUsers, getPageSize, getCurrentPage, getTotalUsersCount, getIsFetching, getIsFollowingInProgress} from '../../redux/users-selectors'

class UsersApiContainer extends React.Component {
  componentDidMount() {
    this.props.requestUsers(this.props.currentPage, this.props.pageSize);
  }
  
  onPageNumberChanged = (pageNumber) => {
    this.props.changePageNumber(pageNumber, this.props.pageSize, pageNumber);
  }
  
  render() {
    return (
    <>
      {this.props.isFetching 
        ? <Preloader/>
        : <Users
            totalUsersCount={this.props.totalUsersCount}
            pageSize={this.props.pageSize}
            currentPage={this.props.currentPage}
            usersData={this.props.usersData}
            onPageNumberChanged={this.onPageNumberChanged}
            follow={this.props.follow}
            unFollow={this.props.unFollow}
            isFollowingInProgress={this.props.isFollowingInProgress}
            toggleIsFollowingProgress={this.props.toggleIsFollowingProgress}
          />
      }
    </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    usersData: getUsers(state),
    pageSize: getPageSize(state),
    currentPage: getCurrentPage(state),
    totalUsersCount: getTotalUsersCount(state),
    isFetching: getIsFetching(state),
    isFollowingInProgress: getIsFollowingInProgress(state),
  }
}


export default compose(
  connect(mapStateToProps, {follow, unFollow, setCurrentPage, toggleIsFollowingProgress, requestUsers, changePageNumber,}),
)(UsersApiContainer);
